	module.exports = {"data":{"markdownRemark":{"html":"<p>看到一篇谈对国外\"中国威胁论\"的综述文章。在中国经济发展起来之后，这种舆论逐渐上升，成为有一定的市场的学说。主要是说中国未来可能是世界上综合经济实力最强的国家，因而会对周围国家构成一种威胁。说中国在发展军事力量。这是一种奇怪的言论，中国经济不发展，周围一片挖苦声，经济要发展了，又害怕。在中国周围，比中国的军事力量强大的国家还有，但是不是一种\"威胁\"。日本的军事费用在世界上是第二，但是不是\"威胁\"。这种舆论在日本有一定的市场。</p>\n<p>这种\"中国威胁论\"，大可不必理会。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0188.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0188.md","frontmatter":{"title":"11月17日 星期四"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0188.md absPath of file >>> MarkdownRemark"}}

